.header {
    width: 100%;
    height: 3rem;
    display: flex;
    justify-content: center;
    align-items: center;
    text-align: center;
    font-weight: bold;
    background-color: black;
    border-bottom: 2px solid grey;
  }
  
  .header > .button {
    position: absolute;
    top: 0.25rem;
    right: 0.25rem;
  }